import { forwardRef, PropsWithoutRef } from 'react';
import styled from 'styled-components';

import { defaultThemeProp } from '../../theme';
import { Attributes, NoChildrenProp, OmitStrict, RefElement } from '../../types';
import Button from '../Button';
import Configuration from '../Configuration';
import { useTheme } from '../../hooks';

export interface DangerButtonProps
  extends NoChildrenProp,
    OmitStrict<Attributes<'button'>, 'children'> {
  /** Text content for the button. */
  label: NonNullable<HTMLButtonElement['textContent']>;
  /**
   * Elevates emphasis for button style.
   * @default false
   */
  primary?: boolean;
}

export const StyledDangerButton = styled(Button)``;

StyledDangerButton.defaultProps = defaultThemeProp;

const DangerButton = forwardRef<RefElement<DangerButtonProps>, PropsWithoutRef<DangerButtonProps>>(
  function DangerButton({ label, primary = false, ...restProps }, ref) {
    const theme = useTheme();

    return (
      <Configuration
        theme={{
          base: {
            palette: {
              interactive: theme.base.palette.urgent
            }
          }
        }}
      >
        <StyledDangerButton {...restProps} variant={primary ? 'primary' : undefined} ref={ref}>
          {label}
        </StyledDangerButton>
      </Configuration>
    );
  }
);

export default DangerButton;
